import React, { Component } from 'react';
import * as _ from 'ramda';
import './CosmicData.css';
import './PlayerInfo.css';

class PlayerInfo extends Component {
    getPlayerDescription = (playerName) => {
        const playerDescriptions = {
            "Main": "Ppelaaja",
            "Ally": "Liittolainen",
            "Main, Ally": "Ppelaaja tai liittolainen",
            "Any": "Kuka tahansa",
            "Offensive ally": "Hykkyksen liittolainen",
            "Offense": "Hykkv ppelaaja",
            "NOT Main, NOT Ally": "EI ppelaaja, EI liittolainen",
            "Offense, Ally": "Hykkv ppelaaja tai liittolainen",
            "NOT Main": "EI ppelaaja",
            "Defense": "Puolustava ppelaaja"
        };

        if (_.has(this.props.player, playerDescriptions)) {
            return playerDescriptions[this.props.player];
        }

        return this.props.player;
    }

        getTimingDescription = (when) => {
        const timingDescriptions = {
            "Start turn": "Start turn (Vuoron aloitus)",
            "Regroup": "Regroup (Vrvys)",
            "Destiny": "Destiny (Kohteen valinta)",
            "Launch": "Launch (Alusten lhettminen)",
            "Alliance": "Alliance (Liittoutumat)",
            "Planning": "Planning (Korttien valinta)",
            "Reveal": "Reveal (Korttien paljastaminen)",
            "Resolution": "Resolution (Lopputulos)",
            "Any": "Milloin tahansa",
        };

        if (_.has(this.props.when, timingDescriptions)) {
            return timingDescriptions[this.props.when];
        }

        return this.props.player;
    }

    render() {
        return (
            <div>
                <div className="player-role">
                    <span className="capitalize">
                        {this.getPlayerDescription(this.props.player)}
                    </span>
                </div>
                <div className="player-phase">
                    <span className="capitalize white">
                        {this.getTimingDescription(this.props.when)}
                    </span>

                </div>
            </div>
        );
    }
}

export default PlayerInfo;


